@font-face {
  font-family: "Motserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Motserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-display: swap;
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Motserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium-Medium.woff") format("woff");
  font-display: swap;
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "FuturaLT";
  src: url("../fonts/FuturaLT-Bold.woff2") format("woff2"), url("../fonts/FuturaLT-Bold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  width: 1440px;
  font-size: 16px;
  font-weight: 500;
  color: #a4a4a4;
  background-color: #ffffff;
  font-family: "Montserrat", "Arial", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
  user-select: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  width: 100%;
  max-width: 1023px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
  margin: 0;
  margin-top: 8px;
  margin-bottom: 8px;
}
@media (min-width: 1024px) {
  .main-header {
    max-width: 1439px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    max-width: 1440px;
    padding-left: 130px;
    padding-right: 130px;
  }
}

.main-header__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.main-header__logo {
  margin-right: 61px;
}

.main-header__phone-button {
  display: none;
}

.main-header__phone {
  order: 1;
  margin-right: 24px;
}

.main-header__toggle {
  order: 2;
}

.main-nav {
  display: block;
  position: absolute;
  top: -100vw;
  left: 0;
  width: 100%;
  margin: 0;
  visibility: hidden;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.work__slider {
  display: flex;
}

.locations__slider {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 28px;
}

.services__photo-wrapper {
  display: grid;
}

.price__list {
  display: flex;
}

/* .additional-services__button-container {
	display: flex;
} */
.promo__wrapper {
  display: flex;
}

.review__list {
  display: flex;
}

.main-footer__container {
  display: flex;
}

.footer-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.social__list {
  display: flex;
}